.userInfo {
  display: flex;
  gap: 2rem;
}

.userInfo > div:first-child {
  width: 30%;
}

.userData {
  display: grid;
  gap: 1rem;
}

.userName {
  font-size: 1.5rem;
  font-weight: 700;
}

.userDataItem {
  display: flex;
  column-gap: .5rem;
}

.userAvatar {
  display: block;
}

.errorMsg {
  color: #1d415e;
  font-size: 1.5rem;
}